                <p>{{ slideGroups[currentGroup].description }}</p>
                <router-link :to="slideGroups[currentGroup].link" class="slide-button">
                  {{ slideGroups[currentGroup].buttonText }} <i class="fas fa-arrow-right"></i>
                </router-link>
              </div>
            </transition>
          </div>
